<template>
  <div>
    <v-dialog v-model="dialog_login" persistent max-width="600px">
      <v-btn slot="activator" dark></v-btn>
      <v-card>
        <v-card-title>
          <span class="headline"></span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field :rules="rules" v-model="loginUserName" label="User name" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  :rules="rules"
                  v-model="loginUserPasswd"
                  label="Password"
                  type="password"
                  required
                ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog_login = false"></v-btn>
          <v-btn color="blue darken-1" flat @click="login"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog_register" persistent max-width="600px">
      <v-btn slot="activator" dark></v-btn>
      <v-card>
        <v-card-title>
          <span class="headline"></span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field :rules="rules" v-model="registerUserName" label="User name" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  :rules="rules"
                  v-model="registerUserPasswd"
                  label="Password"
                  type="password"
                  required
                ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog_register = false"></v-btn>
          <v-btn color="blue darken-1" flat @click="submit()"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { eventBus } from "../main";
export default {
  data: () => ({
    rules: [v => !!v || "can not empty"],
    dialog_login: false,
    dialog_register: false,
    loginUserName: "",
    loginUserPasswd: "",
    registerUserName: "",
    registerUserPasswd: ""
  }),
  methods: {
    login() {
      const login_path = "http://yiyu0x.tk:3000/login";
      // const login_path = 'http://localhost:3000/login'

      const data = {
        username: this.loginUserName,
        password: this.loginUserPasswd
      };

      if (this.loginUserName == "" || this.loginUserPasswd == "") {
        eventBus.$emit("LoginStatus", 0);
        return;
      }
      fetch(login_path, {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data)
      })
        .then(function(response) {
          return response.json();
        })
        .then(jsonData => {
          if (jsonData.status == 1) {
            // 
            this.dialog_login = false;
            console.log("event.bus.emit --> LoginStatus");
            eventBus.$emit("LoginStatus", jsonData);
          } else {
            // 
            eventBus.$emit("LoginStatus", jsonData);
            console.log(" ");
          }
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    submit() {
      const register_path = "http://yiyu0x.tk:3000/register";
      // const register_path = "http://localhost:3000/register";
      this.dialog_register = false;

      const data = {
        username: this.registerUserName,
        password: this.registerUserPasswd
      };

      if (this.registerUserName == "" || this.registerUserPasswd == "") {
        eventBus.$emit("RegisterStatus", 0);
        return;
      }
      fetch(register_path, {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data)
      })
        .then(function(response) {
          return response.json();
        })
        .then(jsonData => {
          if (jsonData.status == 1) {
            // 
            // console.log('')
            eventBus.$emit("RegisterStatus", 1);
          } else {
            // 
            console.log(" ");
            eventBus.$emit("RegisterStatus", 0);
          }
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>